:root {
  --darkBlue: hsl(233, 26%, 24%);
  --limeGreen: hsl(136, 65%, 51%);
  --rightCyan: hsl(192, 70%, 51%);
  --grayishBlue: hsl(233, 8%, 62%);
  --lightGrayishBlue: hsl(220, 16%, 96%);
  --veryLightGray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-inter: "Public Sans", sans-serif;
}

/*
  * use break points for mobile first approach
*/
/*
  * use break points for desktop first approach
*/
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  line-height: 1.5;
  text-rendering: optimizeSpeed;
  background-color: var(--veryLightGray);
}

img {
  display: block;
  width: 100%;
}

ul,
li {
  list-style: none;
}

.btn {
  font-weight: 700;
  border: none;
  border-radius: 1.25rem;
  color: var(--white);
  padding: 0.625rem 1.875rem;
  text-transform: capitalize;
  background: linear-gradient(to right, var(--limeGreen), var(--rightCyan));
  opacity: 1;
  cursor: pointer;
}
.btn:hover {
  opacity: 0.8;
  transition: all 100ms ease-out;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  color: var(--darkBlue);
  text-decoration: none;
}

.navbar {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 4.0625rem;
  margin: 0 auto;
  width: 100%;
  padding: 0 1.5625rem;
  background-color: var(--white);
  z-index: 11;
}
@media screen and (min-width: 40rem) {
  .navbar {
    padding: 0 3.125rem;
  }
}
@media screen and (min-width: 64rem) {
  .navbar {
    padding: 0 6.5625rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .navbar {
    padding: 0 10.3125rem;
  }
}
.navbar__logo {
  width: 8.75rem;
  z-index: 11;
}
.navbar__logo img {
  display: block;
  width: 100%;
}
.navbar__menu {
  width: 1.5625rem;
  height: 1.375rem;
  padding-top: 0.0625rem;
  z-index: 11;
}
.navbar__menu span {
  display: block;
  width: 100%;
  height: 0.1875rem;
  background: var(--darkBlue);
}
.navbar__menu span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
@media screen and (min-width: 64rem) {
  .navbar__menu {
    display: none;
  }
}
.navbar .show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
  transition: all ease-in-out 300ms;
}
.navbar__links_container {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 100%;
  left: 0;
  transform: translateY(-10%);
  font-size: 1rem;
  background: linear-gradient(var(--darkBlue), rgba(0, 0, 0, 0.3));
  visibility: hidden;
  opacity: 0.1;
  transition: all ease-in-out 100ms;
  z-index: -1;
}
@media screen and (min-width: 64rem) {
  .navbar__links_container {
    width: auto;
    position: static;
    visibility: visible;
    opacity: 1;
    height: auto;
    transform: translateY(0);
    background: transparent;
  }
}
.navbar__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 0;
  margin: 1.5625rem;
  border-radius: 0.3125rem;
  background-color: #fff;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}
.navbar__links .link {
  position: relative;
  margin: 0.9375rem auto;
  text-transform: capitalize;
}
.navbar__links .link > a {
  font-weight: 500;
  color: var(--grayishBlue);
}
.navbar__links .link:hover:after {
  position: absolute;
  content: "";
  margin-top: 1.125rem;
  top: 100%;
  left: 0;
  height: 0.3125rem;
  width: 100%;
  background: linear-gradient(to right, var(--limeGreen), var(--rightCyan));
  animation: showUnderline ease-in 100ms;
}
@media screen and (min-width: 64rem) {
  .navbar__links .link {
    margin-right: 1.875rem;
  }
}
@media screen and (min-width: 64rem) {
  .navbar__links {
    background-color: transparent;
    flex-direction: row;
    justify-content: space-around;
  }
}
.navbar .btn-nav {
  display: none;
  font-size: 0.875rem;
}
@media screen and (min-width: 64rem) {
  .navbar .btn-nav {
    display: block;
  }
}

@keyframes showUnderline {
  0% {
    transform: scalex(0);
  }
  100% {
    transform: scalex(1);
  }
}
.hero {
  background-color: var(--veryLightGray);
}
@media screen and (min-width: 40rem) {
  .hero {
    min-height: 34.875rem;
  }
}
@media screen and (min-width: 64rem) {
  .hero {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 28.625rem;
    justify-content: center;
  }
}
@media screen and (min-width: 87.5rem) {
  .hero {
    min-height: 41.125rem;
  }
}
.hero__image {
  position: relative;
  background: url("../../../images/bg-intro-mobile.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  min-height: 17.8125rem;
}
.hero__image::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: url("../../../images/image-mockups.png");
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center bottom;
}
@media screen and (min-width: 40rem) {
  .hero__image {
    background: url("../../../images/bg-intro-desktop.svg");
    background-position: 15% 68%;
  }
  .hero__image::after {
    background-size: 50%;
    top: 0%;
    height: 108%;
    background-position: 50% 50%;
  }
}
@media screen and (min-width: 64rem) {
  .hero__image {
    background: url("../../../images/bg-intro-desktop.svg");
    background-position: 17% 83%;
    background-size: 150%;
  }
  .hero__image::after {
    background-position: -100% 100%;
    background-size: 111%;
    height: 161%;
    top: -150px;
  }
}
@media screen and (min-width: 87.5rem) {
  .hero__image {
    background: url("../../../images/bg-intro-desktop.svg");
    background-position: 16% 75%;
    background-size: 150%;
  }
  .hero__image::after {
    height: 128%;
    background-size: 105%;
    background-position: -225% bottom;
    background-position-x: -225%;
    background-position-y: bottom;
    top: 0%;
  }
}
.hero__text {
  align-self: center;
  margin: 3.125rem 2.1875rem;
}
.hero__text h1 {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 300;
  color: var(--darkBlue);
}
@media screen and (min-width: 64rem) {
  .hero__text h1 {
    text-align: start;
    font-size: 3.125rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .hero__text h1 {
    text-align: start;
    font-size: 3.75rem;
  }
}
.hero__text p {
  color: var(--grayishBlue);
  font-size: 0.875rem;
  line-height: 1.7;
}
@media screen and (min-width: 40rem) {
  .hero__text p {
    text-align: center;
    font-size: 0.9375rem;
  }
}
@media screen and (min-width: 64rem) {
  .hero__text p {
    text-align: left;
    font-size: 1rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .hero__text p {
    font-size: 1.25rem;
  }
}
.hero__text .btn-hero {
  display: block;
  margin: 1.25rem auto;
  padding: 0.625rem 2.5rem;
  font-size: 0.9375rem;
  font-weight: 300;
  border-radius: 1.5625rem;
}
@media screen and (min-width: 64rem) {
  .hero__text .btn-hero {
    display: inline-block;
  }
}
@media screen and (min-width: 40rem) {
  .hero__text {
    margin: 3.125rem;
  }
}
@media screen and (min-width: 64rem) {
  .hero__text {
    order: -1;
    margin-left: 6.25rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .hero__text {
    margin-left: 10.625rem;
  }
}

.feature {
  background-color: var(--lightGrayishBlue);
  text-align: center;
  padding: 4.375rem 0;
}
.feature .container {
  width: 85%;
  margin: 0 auto;
}
@media screen and (min-width: 64rem) {
  .feature .container {
    width: 84%;
  }
}
@media screen and (min-width: 87.5rem) {
  .feature .container {
    width: 78%;
  }
}
.feature__text {
  margin: 0 auto;
  max-width: 38.75rem;
}
.feature__text h2 {
  font-size: 1.875rem;
  font-weight: 300;
}
.feature__text p {
  color: var(--grayishBlue);
  font-weight: 500;
}
@media screen and (min-width: 64rem) {
  .feature__text {
    text-align: left;
    margin-left: 0;
    margin-right: auto;
  }
}
.feature__grid {
  display: grid;
  justify-content: center;
  align-content: center;
}
@media screen and (min-width: 40rem) {
  .feature__grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1.25rem;
  }
}
@media screen and (min-width: 64rem) {
  .feature__grid {
    text-align: left;
  }
}
.feature__grid_item img {
  width: 4.6875rem;
  margin: 2.1875rem auto;
}
@media screen and (min-width: 64rem) {
  .feature__grid_item img {
    width: 5.3125rem;
    margin-right: auto;
    margin-left: 0;
  }
}
@media screen and (min-width: 87.5rem) {
  .feature__grid_item img {
    width: 5.9375rem;
  }
}
.feature__grid_item h2 {
  font-size: 1.25rem;
  font-weight: 300;
}
@media screen and (min-width: 64rem) {
  .feature__grid_item h2 {
    font-size: 0.9375rem;
    text-align: left;
  }
}
@media screen and (min-width: 87.5rem) {
  .feature__grid_item h2 {
    font-size: 1.875rem;
  }
}
.feature__grid_item h3 {
  font-size: 1.25rem;
  font-weight: 300;
}
@media screen and (min-width: 64rem) {
  .feature__grid_item h3 {
    font-size: 1.125rem;
    text-align: left;
  }
}
@media screen and (min-width: 87.5rem) {
  .feature__grid_item h3 {
    font-size: 1.375rem;
  }
}
.feature__grid_item P {
  font-weight: 300;
  font-size: 0.8125rem;
  color: var(--grayishBlue);
}
@media screen and (min-width: 64rem) {
  .feature__grid_item P {
    font-size: 0.9375rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .feature__grid_item P {
    font-size: 1.0625rem;
  }
}

.articles {
  width: 80%;
  margin: 0 auto;
  padding: 5rem 0;
  background-color: var(--veryLightGray);
}
@media screen and (min-width: 64rem) {
  .articles {
    width: 84%;
  }
}
@media screen and (min-width: 87.5rem) {
  .articles {
    width: 78%;
  }
}
.articles h2 {
  font-size: 1.875rem;
  font-weight: 300;
  color: var(--darkBlue);
}
.articles .card-grid {
  display: grid;
  margin-bottom: 20px;
}
@media screen and (min-width: 40rem) {
  .articles .card-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1.25rem;
  }
}
.articles .card {
  background-color: var(--white);
  border-radius: 5px;
  overflow: hidden;
}
.articles .card__head {
  background-color: var(--white);
  min-height: 12.5rem;
}
.articles .card__head img {
  object-fit: cover;
  display: block;
  width: 100%;
  height: 200px;
}
.articles .card__body {
  margin: 2.1875rem;
  color: var(--grayishBlue);
  overflow: hidden;
}
.articles .card__body h4 {
  margin-top: 1.875rem;
  color: var(--darkBlue);
  font-size: 1.125rem;
  font-weight: 300;
}
@media screen and (min-width: 64rem) {
  .articles .card__body h4 {
    font-size: 0.9375rem;
  }
}
.articles .card__body span {
  font-size: 0.75rem;
}
@media screen and (min-width: 64rem) {
  .articles .card__body span {
    font-size: 0.6875rem;
  }
}
.articles .card__body p {
  font-size: 0.8125rem;
}
@media screen and (min-width: 64rem) {
  .articles .card__body p {
    font-size: 0.75rem;
  }
}